.sidebar {
  min-height: 100%;
  width: 250px;
  position: fixed;
  z-index: 1030;
  top: 0;
  left: 0;
  background-color: var(--primary);
  color: var(--white);
  overflow-x: hidden;
  transition: 0.5s;
  -webkit-transition: 0.5s;
}

.sidebar .header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  max-height: 20%;
  background-color: var(--primary);
  font-size: 1.5rem;
}

.sidebar .header i {
  font-size: 2rem;
}

.sidebar .btn-close {
  position: absolute;
  top: 2px;
  left: 5px;
}

.sidebar .btn-minimize {
  position: absolute;
  top: 2px;
  right: 5px;
}

.sidebar .minimized {
  font-size: 2rem;
}

.sidebar i,
.sidebar span {
  transition: 0.5s;
}

.sidebar a {
  padding: 6px 8px 6px 6px;
  text-decoration: none;
  font-size: 1rem;
  color: var(--light);
  display: flex;
  align-items: center;
}

.sidebar .item {
  padding: 6px 8px 6px 6px;
  font-size: 1rem;
  color: var(--light);
  display: flex;
  align-items: center;
  cursor: pointer;
}

.sidebar .item:hover,
.sidebar .item-link:hover,
.sidebar .active {
  color: var(--dark);
  background-color: var(--light);
}

.sidebar .subitem {
  font-size: 1rem;
  padding-left: 20px;
}

.sidebar .subitem .minimized {
  font-size: 1.2rem;
  padding-left: 5px;
}

@media (min-width: 576px) {
  .sidebar {
    position: relative;
  }
}
